.gallery__picture {
  margin-bottom: 10px;

  img{
    max-width:100%;
  }
}

.navbar-brand{
  height: auto !important;
  padding: 8px 15px;
}

.ct-footer--extended{
  background-color: #efefef;
}

.ct-footer--extended span{
  opacity: 1;
}

.form-control{
  background-color: #fefefe;
  border-color: #f9f9f9;
}

.navbar{
  background-color: #fefefe;
  font-weight: bold;
}

.navbar .navbar-nav li a{
  // font-weight: normal;
}

.ct-footer--extended span, .ct-footer--extended li{
  color: inherit;;
}

.ct-links a{
  color: inherit;
}

.navbar .navbar-nav li{
  border: 0px;
}

.navbar .navbar-nav li:hover > a,
.navbar .navbar-nav li:focus > a {
  background-color: inherit;
}

.ct-headerText--normal .form-control{
  width: 100%;
}

.select2-dropdown {
  z-index: 9001;
}

