.page_title{
  font-size: 1.5em;
  font-weight: bold;
  margin-bottom: 20px;
}


.panel-heading{
  font-weight: bold;
}

.detail-row{
  padding: 10px 0px;
  border-bottom: 1px solid #eeeeee;
  margin-bottom: 5px;
}

.detail-row-last{
  border-bottom: 0px;
}

.fav_heart{
  background: #ff0000;
}

.select2-container--default .select2-selection--single{
  height: 40px;
}

.select2-container--default .select2-selection--single .select2-selection__rendered{
  line-height: 40px;
}

.hero-text{
  text-shadow: 1px 1px 1px #000;
}


.hero-text .highlight{
  color: #00CEFF;
}

.ct-itemProducts .ct-main-text{
  height:125px;
  overflow:hidden;
}

.ct-productDetails .ct-u-displayTableCell{
  padding: 10px 0px;
}

.ct-productDetails .ct-u-displayTableCell span{
  line-height: 0px;
}

.modal{
  z-index: 9999;
}

.instruction li{
  border-top: 0px;
  padding-bottom: 10px;
  text-align: -webkit-match-parent;
}

.instruction li:last-child{
  border-bottom: 0px;
}

.instruction{
  margin-top: 20px;
  list-style-type: none;
  padding-left:0;
}

