@media (max-width: 1200px){

  .navbar-collapse.collapse {
      width: auto;
  }

  .navbar-header {
      float: left;
  }
}

@media (max-width: 992px){
  .ct-headerText--normal h1, .ct-headerText--normal h2, .ct-headerText--normal h3, .ct-headerText--normal h4, .ct-headerText--normal h5, .ct-headerText--normal h6{
      margin-top: 60px;
  }
}

.record-info-bar{
  font-size: 12px;
  padding: 10px 0px;
  margin-bottom: 20px;
}

.record-info-bar ul{
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.record-info-bar li {
    display: inline;
    margin-right: 20px;
}

.modal-confirm{
  text-align: center;
  margin: 100px 0px;
}

.progress{
  height: 40px;
}

.progress-bar{
  line-height: 40px;
}

.progress-bar a{
  color: #ffffff;
}

.ct-footer--extended h1, .ct-footer--extended h2, .ct-footer--extended h3, .ct-footer--extended h4, .ct-footer--extended h5, .ct-footer--extended h6{
  color: #000000;
}

.form-group{
  margin-bottom: 15px;
}

.ct-headerText--normal span{
  display:inline-block;
}

.ct-gallery{
  overflow:visible;
}

.ct-navigationThumbnail{
  bottom: -50px;
}

.ct-navigationThumbnail .owl-next{
  border-left: 0px;
}
